import {RookLoggingServiceNotFound} from "../../exceptions";

export default class LocationLogHandler {
    constructor(configuration, factory) {}

    addAug(triggerServices, aug) {
        let loggingService = triggerServices.getService("loggingService");

        if (loggingService === undefined) {
            throw new RookLoggingServiceNotFound()
        }

        loggingService.addLoggingAug(aug);
    }
}

LocationLogHandler.jsonName = "log_handler";
